<template>
  <div class="teacher_container">
    <div class="teacher_top">
      <div class="teacher_top_left" v-on:click="backHome">&lt;</div>
      <div class="teacher_top_right">讲师详情</div>
    </div>
    <div class="teacher_main">
      <div class="teacher_main_content">
        <div>
          <img :src="teacherDetails.avatar" class="teacher_main_content_icon" />
        </div>
        <div class="teacher_main_content_right">
          <div class="teacher_main_content_right_top">
            <span>{{teacherDetails.teacher_name}}</span>
            <span
              style="font-size:14px;color:orange;margin-left:10px;"
            >{{teacherDetails.level_name}}</span>
          </div>
          <div class="teacher_main_content_right_bottom">
            <span>{{teacherDetails.sex | teacherSex}}</span>
            <span style="margin-left:10px;">{{teacherDetails.teach_age}}年教龄</span>
          </div>
        </div>
        <div class="teacher_main_content_btn">
          <button :class="showGuid?'':'btn'" v-on:click="guidShow">{{guid}}</button>
        </div>
      </div>
    </div>

    <div class="teacher_content">
      <van-tabs
        v-model="active"
        @click="onClick"
        line-width="20px"
        color="orange"
        title-active-color="orange"
      >
        <van-tab title="讲师介绍" style="color:grey">
          <!-- 杨德胜 -->
          <div v-if="this.$route.query.id==3">
            <div class="teacher_content_one" v-for="(item,index) in schoolAge" :key="index">
              <div>
                <span style="margin-left:10px;">{{item.attr_name}}</span>
                <span
                  v-for="(v,index) in item.attr_value"
                  :key="index"
                  style="margin-left:30px;"
                >{{v.attr_value_name}}</span>
              </div>
            </div>
          </div>
          <!-- 卫文星,马学斌-->
          <div v-if="this.$route.query.id==191||this.$route.query.id==79">
            <div>
              <div style="width:100%;height:50px;line-heihgt:50px;margin-top:10px;">
                <span style="margin-left:10px;">{{teachAge.attr_name}}</span>
                <span
                  style="margin-left:30px;"
                  v-for="(item,index) in teachAge.attr_value"
                  :key="index"
                >{{item.attr_value_name}}</span>
              </div>
              <div style="width:100%;height:50px;line-heihgt:50px;margin-top:10px;">
                <span style="margin-left:10px;">{{salePrice.attr_name}}</span>
                <span
                  style="margin-left:30px;"
                  v-for="(item,index) in salePrice.attr_value"
                  :key="index"
                >{{item.attr_value_name}}</span>
              </div>
            </div>
          </div>

          <!-- 老师简介 -->
          <div class="teacher_introduct">
            <div class="teacher_introduct_left">老师简介</div>
            <div class="teacher_introduct_right">{{introduct}}</div>
          </div>
        </van-tab>
        <van-tab title="主讲课程">
          <div
            class="teacher_mainCourse"
            v-for="(item,index) in evaluate"
            :key="index"
            v-on:click="toCourseDetails(item)"
          >
            <div class="teacher_mainCourse_content">
              <div style="font-size:16px;">{{item.title}}</div>
              <div style="font0size:12px;color:grey">共{{item.total_periods}}课时</div>
              <div
                v-for="(v,index) in item.teachers_list"
                :key="index"
                class="teacher_mainCourse_icon"
              >
                <img :src="v.teacher_avatar" alt style="width:30px;height:30px;border-radius:100%" />
                <span style="margin-left:10px;">{{v.teacher_name}}</span>
              </div>
              <div class="teacher_mainCourse_footer">
                <div style="margin-left:10px;">{{item.sales_num}}人已报名</div>
                <div
                  :class="item.price==0?'teacher_mainPrice':'teacher_mainPrice_active'"
                >{{item.price | price}}</div>
              </div>
            </div>
          </div>
        </van-tab>
        <!-- 学员评价 -->
        <van-tab title="学员评价">
          <div class="teacher_comment_none" v-show="showNone">
            <div class="teacher_comment_image">
              <img src="../assets/comment.png" alt class="teacher_comment_icon" />
            </div>
            <div class="teacher_commit_no">暂无评论</div>
          </div>
          <div class="teacher_comment_block" v-show="showBlock">
            <div class="teacher_comment_tag_container">
              <div
                class="teacher_comment_tah_items"
                v-for="(item,index) in tag"
                :key="index"
              >{{item.title}}({{item.count}})</div>
            </div>
            <div
              class="teacher_comment_tag_comment"
              v-for="(item,index) in commitList.list"
              :key="index"
            >
              <div class="teacher_comment_tag_comment_left">
                <img :src="item.avatar" alt class="teacher_comment_icon1" />
              </div>
              <div class="teacher_comment_tag_comment_right">
                <div class="teacher_comment_tag_container">
                  <div
                    class="teacher_comment_right"
                    v-for="(item,index) in tag"
                    :key="index"
                  >{{item.title}}</div>
                </div>
                <div style="color:grey;">{{commitList.list[0].content}}</div>
              </div>
            </div>
          </div>
        </van-tab>
      </van-tabs>
    </div>

    <div class="teacher_footer">立即预约</div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      schoolAge: [],
      teacherDetails: [],
      active: 0,
      teachAge: [],
      salePrice: [],
      introduct: [],
      evaluate: [],
      comment: [],
      tag: [],
      showNone: false,
      showBlock: false,
      commitList: [],
      guid:'关注',
      showGuid:false
    };
  },
  methods: {
    backHome() {
      this.$router.back();
    },
    guidShow(){
        this.$http.get('/api/app/teacher/collect/'+this.$route.query.id).then((res)=>{
        window.console.log(res.data.data)
        if(res.data.data.flag===1){
          this.guid='已关注'
          this.showGuid=false
        }else if(res.data.data.flag===2){
          this.guid='已关注'
          this.showGuid=true
        }
      })
    },
    onClick() {
      if (this.active == 2) {
        if (this.tag.length == 0){
          this.showNone = true;
          this.showBlock = false;
        } else {
          this.showNone = false;
          this.showBlock = true;
        }
      }
    },
    toCourseDetails(item) {
      this.$router.push({
        path: "/courseDetails",
        query: {
          id: item.id
        }
      });
    }
  },
  mounted() {
    this.$http
      .get("/api/app/teacher/info/" + this.$route.query.id)
      .then(res => {
        // window.console.log(res.data.data)
        this.schoolAge = res.data.data.attr;
        this.introduct = res.data.data.intro;
        this.teachAge = this.schoolAge[2];
        this.salePrice = this.schoolAge[3];
        // window.console.log(this.introduct)
        // window.console.log(this.salePrice);
      }),
      this.$http.get(`/api/app/teacher/${this.$route.query.id}`).then(res => {
        // window.console.log(res.data.data.teacher);
        this.teacherDetails = res.data.data.teacher;
      });
    this.$http
      .post("/api/app/teacher/mainCourse", {
        limit: 10,
        page: 1,
        teacher_id: this.$route.query.id
      })
      .then(res => {
        //  window.console.log(res.data.data.list)
        this.evaluate = res.data.data.list;
      });
    this.$http
      .post("/api/app/teacher/comment", {
        limit: 10,
        page: 1,
        teacher_id: this.$route.query.id
      })
      .then(res => {
        // window.console.log(res.data.data);
        this.comment = res.data.data;
        this.tag = this.comment.tag;
        this.commitList = this.comment.comment;
        // window.console.log(this.commitList)
      });
     
      
  },
  filters: {
    teacherSex(val) {
      if (val == 0) {
        return "男";
      }
    },
    price(val) {
      if (val == 0) {
        return "免费";
      } else {
        return `￥${(val / 100).toFixed(2)}`;
      }
    }
  }
};
</script>
