  width: 100%;
  height: 80px;
  /* background:red;; */
  position: absolute;
  top: 40px;
}
.teacher_main_content {
  width: 90%;
  height: 100px;
  background: rgb(243, 243, 243);
  margin: 0 auto;
  display: flex;
  align-items: center;
}
.teacher_main_content_icon {
  width: 40px;
  height: 40px;
  border-radius: 100%;
  margin: 0 20px;
}
