  width: 60px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 90px;
  border-radius: 20px;
  border: 1px solid grey;
  background: rgb(235, 238, 254);
  color: orange;
}
.btn_span{
  margin-left:100px;
  color:grey;
  }
